.header{
    display: -ms-flexbox;
    display: flex;
    -ms-flex-align: center;
        align-items: center;
    -ms-flex-pack: justify;
        justify-content: space-between;
    padding: 1rem 2rem;
    position: -webkit-sticky;
    position: sticky;
    top: 0;
    left: 0;
    background-color: white;
    z-index: 100;
}

.header__left{
    display: -ms-flexbox;
    display: flex;
    -ms-flex-align: center;
        align-items: center;
    -ms-flex: 0.3 1;
        flex: 0.3 1;
}

.header__left__img{
    cursor: pointer;
    height: 2.5rem;
    -o-object-fit: contain;
       object-fit: contain;
    margin-left: 2rem;
}

.header .MuiSvgIcon-root{
    font-size: 2.2rem;
}

.header__center{
    -ms-flex: 0.4 1;
        flex: 0.4 1;
    display: -ms-flexbox;
    display: flex;
    -ms-flex-align: center;
        align-items: center;
    border: 1px solid lightgray;
    border-radius: 3px;
}

.header__center > input{
    -ms-flex: 1 1;
        flex: 1 1;
    height: 2.5rem;
    border: none;
    outline: none;
    padding: 0 1rem;
    font-family: inherit;
    font-weight: 500;
}
.header__center > .MuiSvgIcon-root{
    width: 50px !important;
    border-left: 1px solid lightgray !important;
    padding: 0.5rem 1rem !important;
    -webkit-transition: all 0.1s ease-in;
    -o-transition: all 0.1s ease-in;
    transition: all 0.1s ease-in;
    background-color: #FaFaFa;
    -webkit-backface-visibility: hidden;
            backface-visibility: hidden;
    color: gray !important;
    height: 25px !important;
}

.header__center > .MuiSvgIcon-root:hover{
    background-color: #e4e1e1 !important;
    -webkit-transform: scale(1);
        -ms-transform: scale(1);
            transform: scale(1);
}

.header__center > .MuiSvgIcon-root:active{
    background-color:  #F8F8F8 !important;
}

.header__right{
    -ms-flex: 0.3 1;
        flex: 0.3 1;
    display: -ms-flexbox;
    display: flex;
    -ms-flex-align: center;
        align-items: center;
    -ms-flex-pack: end;
        justify-content: flex-end;
}
.header__right > *{
    margin-right: 1rem !important;
}



